<template>
  <footer class="xy-nav">
    <dl
      v-for="(item,index) in meun"
      :key="index"
      :class="{activeMenu: isActive == item.url}"
      @click="toLink(index,item.url,item)"
    >
      <dt :class="isActive == index ? 'active '+ item.activeIcon : item.icon">
        <div v-if="item.name==='购物车' && msgCount>0" class="num">{{ msgCount }}</div>
      </dt>

      <dd
        :class="isActive == item.routerName ? 'menu-text-active':''"
      >{{ item.name }}</dd>
    </dl>
  </footer>
</template>

<script type='es6'>
import { getCartList } from '@/store/module.js'
export default {
  name: '',
  data() {
    return {
      state: false,
      msgCount: 0,
      meun: [
        {
          name: '首页',
          icon: 'nav-home',
          activeIcon: 'nav-home-active',
          url: 'MallHome'
        },
        {
          name: '分类',
          icon: 'nav-fl',
          activeIcon: 'nav-fl-active',
          url: 'MallCategory'
        },
        {
          name: '购物车',
          icon: 'nav-gwc',
          activeIcon: 'nav-gwc-active',
          url: 'BuyCart'
        },
        {
          name: '我的',
          icon: 'nav-wd',
          activeIcon: 'nav-wd-active',
          url: 'MallMine'
        }
      ],
      isActive: 0
    }
  },
  // activated() {
  //   this.getProductList()
  //   this.toLink('/MallHome')
  // },
  created() {
    this.isActive = this.$store.state.mall.isActive
    this.$bus.$on('deleteSp', () => {
      this.getProductList()
    })
    this.getProductList()
  },
  methods: {
    getProductList() {
      this.asyncApiModule(getCartList).then(res => {
        this.msgCount = 0
        for (let index = 0; index < res.cartsList.length; index++) {
          this.msgCount = this.msgCount + res.cartsList[index].productList.length
        }
      })
        .catch(() => {
        })
    },
    toLink(index, url, item) {
      console.log('item------------------------------------------------', item)
      if (this.isActive === index) {
        return
      } else {
        this.$store.commit('mall/SET_ISACTIVE', index)
      }
      var self = this
      self.$router.push({ path: url })
    }
  }
}
</script>

<style lang="less" scoped>

</style>
